:root {
  scroll-behavior: smooth;
  --font-open-sans: 'Open Sans', sans-serif;
  --font-poppins: 'Poppins', sans-serif;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Poppins', sans-serif;
  font-family: var(--font-poppins);
  font-size: 16px;
}

.container {
  margin: 2rem auto;
  width: 1100px;
}

.slider {
  position: relative;
  padding: 3rem;
  min-height: 300px;
}

.arrow-left:hover, .arrow-right:hover {
  background-color: rgba(0, 0, 0, 0.589);
  color: #fff;
}

.arrow-right {
  position: absolute;
  top: 50%;
  right: 0;
  z-index: 90;
  color: #fff;
  font-size: 2rem;
  cursor: pointer;
}

.arrow-left {
  position: absolute;
  top: 50%;
  left: 0;
  z-index: 90;
  color: #fff;
  font-size: 2rem;
  cursor: pointer;
}

.close-modal {
  position: absolute;
  top: 2%;
  right: 2%;
  z-index: 90;
  color: #fff;
  font-size: 2rem;
  cursor: pointer;
}

.close-modal:hover {
  color: #16151A;
}

.image {
  margin: auto;
  display: block;
  width: 50%;
  height: 50%;
  max-width: 700px;
  object-fit: cover;
}

.slide {
  opacity: 0;
  transition: opacity 500ms linear 0s
}

.slide.active {
  height: 100%;
  opacity: 1;
  transition: opacity 500ms linear 0s;
}

.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  z-index: 9999;
}

.backdrop-img {
  display: block;
  margin: 10px auto 0;
  width: 60%;
  height: 90%;
  object-fit: contain;
}

#caption {
  margin: auto;
  display: block;
  width: 80%;
  max-width: 700px;
  text-align: center;
  color: #ccc;
  padding: 10px 0;
  height: 150px;
}

.hide-desk {
  display: none;
}

.hide-mobile {
  display: block;
}

@media screen and (max-width: 500px) {
  body {
    font-size: 12px;
  }

  .container {
    margin: 1rem;
    width: 90%;
  }

  .hide-mobile {
    display: none;
  }

  .hide-desk {
    display: block;
    position: absolute;
    top: 455px;
    left: 40px;
    margin-top: 25px;
    margin-bottom: 15px;
  }

  .slider {
    position: relative;
    /* height: auto; */
    margin-top: 2.5rem;
    /* margin-bottom: 2.5rem; */
  }

  /* .arrow-right {
    position: absolute;
    top: 200px;
    right: 10px;
    color: #ec4899;
    font-size: 2rem;
    cursor: pointer;
  }
  .arrow-left {
    position: absolute;
    top: 200px;
    left: 10px;
    color: #ec4899;
    font-size: 2rem;
    cursor: pointer;
  } */

  .image {
    width: 100%;
  }

  .backdrop {
    background: rgba(0, 0, 0, 0.7);
  }

  .backdrop img {
    display: block;
    max-width: 100%;
    max-height: 100%;
    margin: 60px auto;
  }
}

